export const fps = 60;

// The index of a bus with a non-editable constant value of 1.
// Used for all "non-connected" inlets.
export const defaultConstantBusIndex = -1;

// The index of the bus where the outputs of all "non-connected"
// modules are sent.
export const nullSendBusIndex = -2;




